<template>
  <ae-main style="background:">
    <ae-header name="Crypto Mæme Voting"></ae-header>
    <div class="container">

      <h5>Submit meme</h5>
      <form>
        meme url <input type="text" v-model="submission.registerLink">
        your name <input type="text" v-model="submission.registerName">
        <br><br>
        <ae-button type="dramatic" size="small" @click="register()">
           submit</ae-button>
      </form>

      <h5>Meme Voting</h5>
      <div class="row"  v-for="(item,index) in this.memes" style="margin-bottom:3em;">
        <div class="two columns"><div style="">.</div></div>
        <div class="two columns">
          <div>
            <h4>{{index+1}}.</h4>
            <ae-badge style="
              display: block;
              flex-grow: 1;
              min-width: 0;
              padding: 14px 13px;
              border: none;
              font-weight: 500;
              line-height: 1.63;
              letter-spacing: .2px;
              font-size:1em;
            ">{{item.votes}} Wei</ae-badge>
          </div>
          <div style="font-size:1.5em; margin-top:0.5em;">+</div>
          <ae-input type="number" placerholder="amount in wei" v-model="voteAmount"/>
          <ae-button type="dramatic" size="small" @click="voteMeme(item.index)">
             vote</ae-button>
        </div>

        <div class="four columns">
          <img :src="item.memeUrl" alt="Girl in a jacket" style="max-width:400px;border-radius:10px;padding:1em; background:white;">
          <div style="text-align:left;">by <b>{{item.creatorName}}</b></div>

        </div>
      </div>


      </div>


      <div>{{getAddress()}}</div>
      <div>{{this.memesLength}}</div>
      </div>
    </div>
  </ae-main>
</template>

<script>
import Web3 from 'web3'
import AeppComponents, { AeMain, aeInput, aeBanner } from '@aeternity/aepp-components'

export default {
  name: 'HelloWorld',
  props: {
    msg: String
  },
  data() {
    return {
      submission:{
        registerLink:'',
        registerName:''
      },
      voteAmount:10,
      contract:{},
      address:'',
      memesLength:4,
      memes:[]
    }
  },
  methods: {
    accounts: function () {
      return this.address;
    },
    register() {
      this.contract.submitMeme(this.submission.registerLink,
        this.submission.registerName , (err, res) => {
        if (err) {
          console.log("error");
        }
      });
    },
    voteMeme(index) {
      console.log("test");
      this.contract.voteMeme(index, {value:this.voteAmount, from:this.address}, (err, res) => {
        console.log(res)
        if (err) {
          console.log("error");
        }
      });
    },
    testLenght(){
      console.log("asd");
      this.contract.getMemesLength( (err,res) => {
        console.log(res);
      });
    },

    getMemesLength() {
      console.log("memelength")
      this.contract.getMemesLength( (err, res) => {
        console.log(res);
        if (res) {
          this.memesLength = res.c[0];
          // var memes = [];
          console.log(res.c[0]);
          this.getMemes();
        } else {
          console.log("error");
        };
      });
      // var inputUrl = this.submission.link;
    },
    orderedMemes(){
      function compare(a,b) {
        if (a.votes > b.votes)
          return -1;
        if (a.votes < b.votes)
          return 1;
        return 0;
      }
      this.memes.sort(compare);

    },
    getMemes(){
      console.log("getmemes");
      console.log(this.memesLength);
      for (let i = 1; i <= this.memesLength; i++){
        this.contract.getMeme(i, (err, res) => {
          if (res[2]) {
            var element = {};
            element.creatorName = res[1];
            element.memeUrl = res[2];
            element.index = i;
            element.votes = res[3].c[0];
            this.memes.push(element);
            this.orderedMemes();
          } else {
            console.log("error");
          }
        });
      }
    },
    getAddress(){
      if (typeof web3 !== 'undefined') {
        web3 = new Web3(web3.currentProvider);
      } else {
        // set the provider you want from Web3.providers
        alert("Please log into metamask");
      }
      web3.eth.defaultAccount = web3.eth.accounts[0];
      this.address = web3.eth.defaultAccount;
      return web3.eth.defaultAccount;
    }
   },
   created() {
     if (typeof web3 !== 'undefined') {
       web3 = new Web3(web3.currentProvider);
     } else {
       // set the provider you want from Web3.providers
       alert("Please log into metamask");
     }
     web3.eth.defaultAccount = web3.eth.accounts[0];
     console.log("hi");
     var contract = web3.eth.contract([
    	{
    		"constant": false,
    		"inputs": [
    			{
    				"name": "_url",
    				"type": "string"
    			},
    			{
    				"name": "_name",
    				"type": "string"
    			}
    		],
    		"name": "submitMeme",
    		"outputs": [],
    		"payable": false,
    		"stateMutability": "nonpayable",
    		"type": "function"
    	},
    	{
    		"constant": false,
    		"inputs": [
    			{
    				"name": "_index",
    				"type": "uint256"
    			}
    		],
    		"name": "voteMeme",
    		"outputs": [],
    		"payable": true,
    		"stateMutability": "payable",
    		"type": "function"
    	},
    	{
    		"constant": true,
    		"inputs": [
    			{
    				"name": "_index",
    				"type": "uint256"
    			}
    		],
    		"name": "getMeme",
    		"outputs": [
    			{
    				"name": "",
    				"type": "address"
    			},
    			{
    				"name": "",
    				"type": "string"
    			},
    			{
    				"name": "",
    				"type": "string"
    			},
    			{
    				"name": "",
    				"type": "uint256"
    			}
    		],
    		"payable": false,
    		"stateMutability": "view",
    		"type": "function"
    	},
    	{
    		"constant": true,
    		"inputs": [],
    		"name": "getMemesLength",
    		"outputs": [
    			{
    				"name": "",
    				"type": "uint256"
    			}
    		],
    		"payable": false,
    		"stateMutability": "view",
    		"type": "function"
    	}
    ]);

      //kovan: 0x1df6909df2af9cf799d4f16153820a9a88ede12f
      //roposten: 0x2e62558ffc45e200c0e08b6e11141a5ff2160175
      this.contract = contract.at('0x1df6909df2af9cf799d4f16153820a9a88ede12f');
      this.address = web3.eth.defaultAccount;
      this.getMemesLength();
    },
 }
</script>

