  margin: 40px 0 0;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
.myButton{
  background:black;
  color:white;
  width:60px;
  padding:0.4em;
  cursor:pointer;
  margin:0.5em;
}
</style>
